import React from 'react'
import { Link } from 'react-router-dom';

const baseHref = process.env.NODE_ENV === 'development' ? '/' : '/webdev-with-reactjs/';

const Login = () => {

    return (
        <div>
            <h1>Login page</h1>

            <Link to={baseHref}>Back Home</Link>
        </div>
    )
}

export default Login
